.profile{
    display: flex;
    flex-direction: column;

    border-radius: 10px;

    box-shadow: 3px 5px 20px 2px rgba(105, 111, 116, 0.491);

    background-color: white;

    width: 100%;
    max-width: 300px;
    margin: 40px auto;

    overflow: hidden;

    font-family: 'Inconsolata';
}
.description{
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 30px 15px;
}
.avatar{
    width: 100px;
    height: 100px;

    border-radius: 50%;
    border: 1px solid rgb(38 130 255);

    padding: 5px;

    margin-top: 30px;
}
.name{
    font-weight: 600;
    font-size: 28px;

}
.tag{
    color: gray;
    margin: 0;

    font-size: 14px;

    color: rgb(38 130 255);

}
.location{
    color: rgb(66, 66, 66);
    margin: 10px 0;

    font-size: 16px;
}
.stats{
    display: flex;
    gap: 1px;

    width: 100%;

    
    padding: 0;
    margin: 0;

    list-style-type: none;
}

.stats li{
    display: flex;
    flex-direction: column;
    align-items: center;

    flex: 1;

    background-color:rgb(38 130 255 / 21%);

    padding: 15px;
}
.label{
    font-size: 12px;
    opacity: 0.6;
    margin-bottom: 5px;

    font-weight: 600;
}

.quantity{
    font-weight: 600;
    font-size: 18px;
}