.item{
    position: relative;

    width: 100px;


    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.name{
    margin: 0;

    font-weight: 600;
    font-size: 20px;
}

.avatar{
    display: block;

    box-shadow: 3px 5px 20px 2px rgba(105, 111, 116, 0.491);
    
    border-radius: 50%;
    border: 3px solid #fff;

    width: 80px;
    height: 80px;

    overflow: hidden;
}

.status{
    position: absolute;
    top: 0px;
    right: 10px;

    width: 15px;
    height: 15px;
    border: 2px solid #fff;
    border-radius: 50%;

    background-color: rgb(132, 132, 132);
}

.online{
    background-color: rgb(4, 230, 4);
}

.offline{
    background-color: rgb(255, 9, 9);
}

